.cardsContainer {
  background-color: var(--bg_body);
  display: grid;
  grid-auto-rows: 300px;
  width: 100%;
  gap: 27px;
  grid-template-columns: repeat(auto-fill, minmax(208px, 1fr));
  align-items: center;
  justify-items: center;
  min-height: calc(100vh - 401px);
  position: relative;
}
